import "./CardTable.css";
import { Card } from './Card.js';

export function CardTable(props) {

  const heartsCards = props.game["hearts"].map( (card,index) => {
    const playedClass = card.played ? "" : "cardtable-notplayed"
    const classes = "cardtable-card cardtable-hearts " +  playedClass
    return <div key={index} className={classes}>
      <Card value={card.value + "H"} hidden={!card.played} />
      </div>
  })
  const clubsCards = props.game["clubs"].map( (card,index) => {
    const playedClass = card.played ? "" : "cardtable-notplayed"
    const classes = "cardtable-card cardtable-clubs " +  playedClass
    return <div key={index} className={classes}>
      <Card value={card.value + "C"} hidden={!card.played} />
      </div>
  })
  const diamondsCards = props.game["diamonds"].map( (card,index) => {
    const playedClass = card.played ? "" : "cardtable-notplayed"
    const classes = "cardtable-card cardtable-diamonds " +  playedClass
    return <div key={index} className={classes}>
      <Card value={card.value + "D"} hidden={!card.played} />
      </div>
  })
  const spadesCards = props.game["spades"].map( (card,index) => {
    const playedClass = card.played ? "" : "cardtable-notplayed"
    const classes = "cardtable-card cardtable-spades " +  playedClass
    return <div key={index} className={classes}>
      <Card value={card.value + "S"} hidden={!card.played} />
      </div>
  })

  return (
    <div className="cardtable-panel">
      <div className="cardtable-container">
        <div className="cardtable-hearts">{ heartsCards }</div>
        <div className="cardtable-clubs">{ clubsCards }</div>
        <div className="cardtable-diamonds">{ diamondsCards }</div>
        <div className="cardtable-spades">{ spadesCards }</div>
      </div>
    </div>
  )
}