import './App.css';
import { MyHand } from './MyHand.js';
import { CardTable } from './CardTable.js';
import { useState, useEffect } from 'react';

function App() {

  console.log("App called")

  const [log,setLog] = useState("")
  const [hand,setHand] = useState([])
  const [player,setPlayer] = useState('...')
  const [ws,setWs] = useState()
  const [game,setGame] = useState(resetGame())
  
  function resetGame() { return {
    "hearts": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ],
    "clubs": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ],
    "diamonds": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ],
    "spades": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ]
    }
  }

  useEffect( () => {

    const cardpack = [
      "AH","2H","3H","4H","5H","6H","7H","8H","9H","10H","JH","QH","KH",
      "AC","2C","3C","4C","5C","6C","7C","8C","9C","10C","JC","QC","KC",
      "AD","2D","3D","4D","5D","6D","7D","8D","9D","10D","JD","QD","KD",
      "AS","2S","3S","4S","5S","6S","7S","8S","9S","10S","JS","QS","KS"
    ]

    function getSuit(value) {

      const suits = {
        'C': '\u2663',
        'D': '\u2666',
        'S': '\u2660',
        'H': '\u2665'
      }
  
      let suit = value.length === 2 ? value.charAt(1) : value.charAt(2);
  
      return suits[suit]
    }

    function getCard(card) {
      let value = card.length === 2 ? card.substring(0,1) : card.substring(0,2);
      let suit = getSuit(card)
      return (value + suit)
    }

    function getNextPlayer(p) {
      const players = [
        "Nancy",
        "John",
        "Allen",
        "Gavin"
      ]

      let index = players.findIndex(plyr => plyr === p) + 1

      index = (index >= players.length) ? 0 : index;

      return (players[index])
    }

    function updateHand(newhand) {
      console.log(`Update hand called with ${newhand}`)
  
      newhand.sort( (a,b) => {
        let ai = cardpack.findIndex(c => a === c)
        let bi = cardpack.findIndex(c => b === c)
        return (ai - bi)
      })
  
      setHand(newhand)
    }
  
    function getWs() {
      var HOST = "wss://jbsevens.herokuapp.com"
      let localWs = new WebSocket(HOST);
      localWs.onmessage = (event) => {
        console.log(event.data)
        let m = JSON.parse(event.data);
        switch(m.type) {
          case 'card':
            setLog(`${m.player} played ${getCard(m.card)} and has ${m.left} card(s) left - ${getNextPlayer(m.player)}'s go...`)
            updateGame(m.card);
            break;
          case 'knock':
            setLog(`${m.player} knocked! - ${getNextPlayer(m.player)}'s go...`)
            break;
          case 'message':
            setLog(`${m.message}`)
            break;
          case 'hand':
            updateHand(m.cards);
            break;
          default:
            console.log("unsupported message type")
        }
      }
      setInterval( () => {
        localWs.send(
          JSON.stringify({
            type: 'ping'
          })          
        )
      },30000)
      return localWs
    }
  
    setWs(getWs())
  },[])

  const sendCard = (card,left) => {
    ws.send(JSON.stringify({
      type: 'card',
      player: player,
      left: left,
      card: card
    }))
  }

  const newgame = () => {
    setPlayer('...')
    setGame(resetGame())
    setHand([])
    ws.send(JSON.stringify({
      type: 'newgame'
    }))  
  }

  const knock = () => {
    ws.send(JSON.stringify({
      type: 'knock',
      player: player
    }))
  }

  function updatePlayer(evt) {

    console.log(`update player called for: ${evt.target.value}`)

    let p = evt.target.value
    sendHandRequest(p)
    setPlayer(p)
  }

  function updateGame(card) {
    setGame( g => {

      let suit = card.length === 2 ? card.charAt(1) : card.charAt(2);
      let cardValue = card.length === 2 ? card.substring(0,1) : card.substring(0,2)
  
      const newTable = {
        hearts: g["hearts"].map( oldcard => {
          if (suit === "H" && cardValue === oldcard.value) return { value: oldcard.value, played: true}
          else return { value: oldcard.value, played: oldcard.played}
        }),
        clubs: g["clubs"].map( oldcard => {
          if (suit === "C" && cardValue === oldcard.value) return { value: oldcard.value, played: true}
          else return { value: oldcard.value, played: oldcard.played}
        }),
        diamonds: g["diamonds"].map( oldcard => {
          if (suit === "D" && cardValue === oldcard.value) return { value: oldcard.value, played: true}
          else return { value: oldcard.value, played: oldcard.played}
        }),
        spades: g["spades"].map( oldcard => {
          if (suit === "S" && cardValue === oldcard.value) return { value: oldcard.value, played: true}
          else return { value: oldcard.value, played: oldcard.played}
        })
      }
    
      return(newTable)    
    })
  }
  
  function sendHandRequest(p) {
    console.log(`send hand request called, player: ${p}`)
    if (p) {
      ws.send(JSON.stringify({
        type: 'hand',
        player: p
      }))  
    }
  }
  
  return (
    <div className="App">
      <div className="app-controls-and-hand">
        <div className="app-controls">
          <div className="app-container">
            <div className="app-playerselecter">
              <div><input type="button" onClick={newgame} value="New Game" /></div>
              <div>
                <label htmlFor="players">Choose a player:</label>
                <select value={player} name="players" id="players" onChange={updatePlayer}>
                  <option value="...">...</option>
                  <option value="Nancy">Nancy</option>
                  <option value="John">John</option>
                  <option value="Allen">Allen</option>
                  <option value="Gavin">Gavin</option>
                </select></div>
              </div>
            <div>
              <div className="app-log">{log}</div>
            </div>
          </div>
        </div>
        <MyHand hand={hand} playCard={sendCard} knock={knock} newgame={newgame}/> 
      </div>
      <CardTable game={game} />
    </div>
  );
}

export default App;
